import "@babel/polyfill";
import gsap from 'gsap';

const { timeline } = gsap;

export function initFormValidation(){
  const form = document.querySelector('.js--form'),
    formWrapper = form.querySelector('.js--form__wrapper'),
    formMessage = form.querySelector('.js--form__message'),
    formSubmit = form.querySelector('.js--form__btn');
  if(!form){
    return;
  }
  let requiredInputs = Array.from(form.querySelectorAll(':required'));
  let valids = new Set(); //  
  requiredInputs.forEach(function(input){
    input.addEventListener('keyup', function(){
      if(this.value){
        valids.add(this);
      } else {
        valids.delete(this);
      };
      if(valids.size >= requiredInputs.length){
        formSubmit.disabled = false
      } else {
        formSubmit.disabled = true;
      };
    })
  });
  form.addEventListener('submit', function(e){
    e.preventDefault(); //    
    //         
    const tl = timeline();
    tl
      .to(formWrapper, 0.5, {
        opacity: 0, ease: 'circ',
        onComplete: function(){
          formWrapper.style.display = 'none';
          formMessage.style.display = 'block';
        }
      })
      .to(formMessage, 0.5,
        {opacity: 1, ease: 'circ'});
    
  })
}