import gsap from 'gsap';
const { timeline } = gsap;

export default function initMenuMobile(){
  const menuMobile = document.querySelector('.js--menu-mobile');
  if(!menuMobile || !window.matchMedia("(max-width: 600px)").matches){
    return;
  };
  const menuMobileObj = {
    state: 'closed' // open/closed
  };
  window.menuMobile = menuMobileObj;
  const layout = document.querySelector('.js--layout'),
    triggers = document.querySelectorAll('.js--menu-mobile__trigger'),
    layer = menuMobile.querySelector('.js--menu-mobile__layer'),
    // overlayBottom = document.querySelector('.js--overlay--bottom'),
    menuItems = menuMobile.querySelectorAll('.js--menu-mobile__item'),
    link = menuMobile.querySelectorAll('.js--link--feedback'),
    lang = menuMobile.querySelectorAll('.js--menu-mobile__lang');
  triggers.forEach(trigger => {
    trigger.addEventListener('click', function(){
      const tl = timeline();
      menuMobile.classList.toggle('overlay--mobile-open');
      window.menuMobile.state = window.menuMobile.state === 'closed' ? 'open' : 'closed';
      // 
      if(window.menuMobile.state === 'open'){
        layout.classList.add('layout--cropped');
        // overlayBottom.classList.add('overlay--bottom-mf');
        if(trigger.classList.contains('js--burger')){
          trigger.classList.add('burger--disabled');
        }
        tl
          .addLabel('openMenuMobile')
          .to( layer, 0.3, { x: '-100%', ease: 'circ' })
          .to(document.querySelector('.js--overlay__col--nav-dots'), 
            0.5, {opacity: 0, ease: 'circ'}, 'openMenuMobile')
          .to( menuItems, 0.3, 
            {opacity: 1, y: 0, ease: 'circ', stagger: 0.1 })
          .to( [lang, link], 0.3, { opacity: 1, y: 0, ease: 'circ', onComplete: () => {
              if(trigger.classList.contains('js--burger')){
                trigger.classList.remove('burger--disabled');
              }
            } 
          });
      }
      // 
      if(window.menuMobile.state === 'closed'){
        layout.classList.remove('layout--cropped');
        //  ,   
        window.popup.form.close();
        if(trigger.classList.contains('js--burger')){
          trigger.classList.add('burger--disabled');
        }
        tl
          .addLabel('closeMenuMobile')
          .to( [lang, link], 0.3, { opacity: 0, y: 30, ease: 'circ' })
          .to( menuItems, 0.3, 
            {opacity: 0, y: 50, ease: 'circ', stagger: {
              each: 0.1, from: "end"
            }, onComplete: function(){
              if(trigger.classList.contains('js--burger')){
                trigger.classList.remove('burger--disabled');
              }
              // overlayBottom.classList.remove('overlay--bottom-mf');
            } }, 'closeMenuMobile')
          .to( layer, 0.3, { x: 0, ease: 'circ' })
          .to(document.querySelector('.js--overlay__col--nav-dots'), 
            0.5, {opacity: 1, ease: 'circ'}, 'closeMenuMobile+=0.5');
      }
    })
  })
}