import "@babel/polyfill";
// let svgCursor = document.getElementById('svg-cursor'),
//     mainLogoCursor = document.getElementById('main-logo-cursor'),
//     cursors = [svgCursor, mainLogoCursor];
export function initCursorCustom() {
  const cursorCustom = document.querySelector('.js--cursor-custom');
  if (!cursorCustom) {
    return;
  }
  document.addEventListener('mousemove', (event) => {
    const arrStyles = [
      `transform: translate3d(${event.clientX}px, ${event.clientY}px, 0)`,
      // `left: ${event.clientX}px`,
      // `top: ${event.clientY}px`,
    ];
    cursorCustom.style.cssText = arrStyles.join(';');
  });
  // ,      .   
  //  
  const elementsToHoverLink = [
    {
      selectors: [
        '.js--cc-hover--link',
        '.os-scrollbar-handle',
      ],
      className: 'cursor-custom--hover-link',
    },
    {
      selectors: [
        '.js--cc-hover--next',
      ],
      className: 'cursor-custom--hover-next',
    },
    {
      selectors: [
        '.js--cc-hover--prev',
      ],
      className: 'cursor-custom--hover-prev',
    },
    {
      selectors: [
        '.js--cc-hover--down',
      ],
      className: 'cursor-custom--hover-down',
    },
  ];
  elementsToHoverLink.forEach((elements) => {
    const { className } = elements;
    elements.selectors.map((selector) => {
      const elementAll = document.querySelectorAll(selector);
      if (!elementAll > 0) {
        return;
      }
      elementAll.forEach((element) => {
        element.addEventListener('mouseover', () => {
          cursorCustom.classList.add(className);
        });
        element.addEventListener('mouseout', () => {
          cursorCustom.classList.remove(className);
        });
      });
    });
  });
}
