import "@babel/polyfill";
export function initPresentationText() {
  const presText = document.querySelector('.js--presentation-text');
  if (!presText) {
    return false;
  }
  const items = presText.querySelectorAll('.js--presentation-text__item'),
    classActive = 'presentation-text__item--active';
  let frames = new Set();
  items.forEach((item) => {
    let dataFrames = item.dataset.frames.split(',');
    dataFrames.forEach((df) => {
      frames.add(df);
    });
  });
  let curIndex = 1,
    framesSize = frames.size;
  setInterval(function(){
    items.forEach((item) => {
      item.classList.remove(classActive);
      let dfArr = item.dataset.frames.split(',');
      if(dfArr.includes(curIndex.toString())){
        item.classList.add(classActive);
      }
    });
    if(curIndex === framesSize){
      curIndex = 1;
    } else {
      curIndex++;
    }
  }, 2000);
}
