import "@babel/polyfill";
import gsap from 'gsap';
import $ from 'jquery';
import rangeslider from 'rangeslider.js';
import OverlayScrollbars from 'overlayscrollbars';

import { checkElementOnScreen } from './utils';

const { timeline } = gsap;

/*   */
const offsetX = 104; //    ,  /
const headerColRight = document.querySelector('.js--header__col--right');

/* 
          (projects)
      .
*/
export function addSectionLinkListener(container=document) {
  const sectionLink = container.querySelector('.js--main__link--to-page');
  if(!sectionLink){
    return;
  }
  sectionLink.addEventListener('mouseenter', onSectionLinkMouseEnter);
  sectionLink.addEventListener('mouseleave', onSectionLinkMouseLeave);
};

/*         (projects) */
export function onSectionLinkMouseEnter(event) {
  const tl = timeline();
  const sectionLink = event.target;
  tl
    .addLabel('bottomBtn')
    .to(sectionLink, 1, {height: 160, ease: 'circ'}, 'bottomBtn');
    // .to(document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'), 1, { width: 50, ease: 'circ' }, 'bottomBtn')
  //      (   )
  let marker = document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited');
  if(marker && sectionLink.dataset){
    if(sectionLink.dataset.sectionIndex){
      const main = document.querySelector('.js--main');
      const sectionsCount = parseInt(main.dataset.sectionsCount, 10);
      const curIndex = parseInt(main.dataset.sectionIndex, 10);
      const nextIndex = parseInt(sectionLink.dataset.sectionIndex, 10);
      if(nextIndex < sectionsCount){
        if(nextIndex > curIndex){
          marker.classList.add('nav-dots__marker--to-next');
        } else {
          marker.classList.add('nav-dots__marker--to-prev');   
        }
        tl
          .to(document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'), 
            1, { width: 50, ease: 'circ' }, 'bottomBtn');
      }
    }
  }
}
/*         (projects) */
export function onSectionLinkMouseLeave(event) {
  const tl = timeline();
  const sectionLink = event.target;
  tl
    .addLabel('bottomBtn')
    .to(sectionLink, 1, {height: 104, ease: 'circ'}, 'bottomBtn');
  //      (   )
  let marker = document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited');
  if(marker && sectionLink.dataset){
    if(sectionLink.dataset.sectionIndex){
      const main = document.querySelector('.js--main');
      const sectionsCount = parseInt(main.dataset.sectionsCount, 10);
      const curIndex = parseInt(main.dataset.sectionIndex, 10);
      const nextIndex = parseInt(sectionLink.dataset.sectionIndex, 10);
      if(nextIndex < sectionsCount){
        if(nextIndex > curIndex){
          marker.classList.remove('nav-dots__marker--to-next');
        } else {
          marker.classList.remove('nav-dots__marker--to-prev');   
        }
        let width;
        if(!window.matchMedia("(max-width: 600px)").matches){
          width = 12;
        } else {
          width = 10;
        }
        tl
          .to(document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'), 
            1, { width: width, ease: 'circ' }, 'bottomBtn');
      }
    }
  }
}

/*       (/) */
export function addSideLinksListeners() {
  const sidenavs = document.querySelectorAll('.js--sidenav');
  if(!window.matchMedia("(max-width: 600px)").matches){
    sidenavs.forEach((sidenav) => {
      sidenav.addEventListener('mouseenter', onSideNavMouseEnter);
      sidenav.addEventListener('mouseleave', onSideNavMouseLeave);
    });
  }
};

/*       (/) */
export function onSideNavMouseEnter(event) {
  const tl = timeline();
  const sideLink = event.target.querySelector('.js--sidenav__link');
  const presentationText = document.querySelector(".js--presentation-text");
  const moColNavDots = document.querySelector(".js--overlay__col--nav-dots");
  const moColContact = document.querySelector(".js--overlay__col--contact");
  const layerNext = document.querySelector(".js--overlay__layer--next");
  const logo = document.querySelector(".js--logo");
  const overlayForScroll = document.querySelector(".js--overlay--for-scroll");
  const titleSection = document.querySelector(".js--main__title--section");
  let titleSectionWrap;
  if(titleSection){
    titleSectionWrap = titleSection.parentNode;
  }
  let offsetSN = offsetX;
  let offsetSign = 0;
  if (sideLink.classList.contains('js--sidenav__link--next')) {
    offsetSign = -1;
  }
  offsetSN *= offsetSign;
  //  
  if (sideLink.classList.contains('js--sidenav__link--prev')) {
    tl.addLabel('forwardPrev')
      .to(sideLink, 1, { x: offsetSN, ease: 'circ' }, 'forwardPrev')
      .to(
        [logo, moColNavDots, titleSectionWrap, overlayForScroll], 
        1, { x: offsetX, ease: 'circ' }, 'forwardPrev')
      .to(document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'), 1, { width: 50, ease: 'circ' }, 'forwardPrev');
      let marker = document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited');
      if(marker){
        marker.classList.add('nav-dots__marker--to-prev');
      }
  }
  //  
  if (sideLink.classList.contains('js--sidenav__link--next')) {
    tl.addLabel('forwardNext')
      .to(sideLink, 1, { x: 0, ease: 'circ' }, 'forwardNext')
      .to(
        [headerColRight, moColContact, layerNext], 
        1, { x: offsetSN, ease: 'circ' }, 'forwardNext')
      .to(presentationText, 1, { x: offsetSN/4, ease: 'circ' }, 'forwardNext')
      .to(document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'), 1, { width: 50, ease: 'circ' }, 'forwardNext');
  }
}
/*       (/) */
export function onSideNavMouseLeave(event) {
  const tl = timeline();
  const sideLink = event.target.querySelector('.js--sidenav__link');
  const presentationText = document.querySelector(".js--presentation-text");
  const moColNavDots = document.querySelector(".js--overlay__col--nav-dots");
  const moColContact = document.querySelector(".js--overlay__col--contact");
  const layerNext = document.querySelector(".js--overlay__layer--next");
  const logo = document.querySelector(".js--logo");
  const overlayForScroll = document.querySelector(".js--overlay--for-scroll");
  const titleSection = document.querySelector(".js--main__title--section");
  let titleSectionWrap;
  if(titleSection){
    titleSectionWrap = titleSection.parentNode;
  }
  let offsetSN = offsetX;
  let offsetSign = 1;
  if (sideLink.classList.contains('js--sidenav__link--prev')) {
    offsetSign = -1;
  }
  offsetSN *= offsetSign;
  //  
  if (sideLink.classList.contains('js--sidenav__link--prev')) {
    tl.addLabel('backwardPrev')
      .to(
        [logo, moColNavDots, titleSectionWrap, overlayForScroll], 
        1, { x: 0, ease: 'circ' }, 'backwardPrev')
      .to(document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'), 1, { width: 12, ease: 'circ' }, 'backwardPrev')
      .to(sideLink, 1, { x: offsetSN, ease: 'circ' }, 'backwardPrev');
      let marker = document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited');
      if(marker){
        marker.classList.remove('nav-dots__marker--to-prev');
      }
  }
  //  
  if (sideLink.classList.contains('js--sidenav__link--next')) {
    tl.addLabel('backwardNext')
      .to(sideLink, 1, { x: offsetSN, ease: 'circ' }, 'backwardNext')
      .to(
        [headerColRight, moColContact, layerNext, presentationText], 
        1, { x: 0, ease: 'circ' }, 'backwardNext')
      .to(document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'), 1, { width: 12, ease: 'circ' }, 'backwardNext');
  }
}

/*   */
export function addScrollListener(container=document) {
  //   
  //  
  let overlayForScroll = container.querySelector('.js--overlay--for-scroll');
  if(overlayForScroll){
    let overlayBottom = document.querySelector('.js--overlay--bottom'),
      header = document.querySelector('.js--header'),
      sectionTitleFixed = container.querySelector('.js--main__title--section-fixed'),
      sectionTitle = container.querySelector('.js--main__title--section'),
      mainSectionTop = container.querySelector('.js--main__section--top'); 
    function fixingHeader(overlayForScroll, overlayBottom, sectionTitle=null, sectionTitleFixed=null){
      if(window.matchMedia('(max-width: 600px)').matches){
        let wrapper = container.querySelector('.js--content.main__wrapper--mobile-pt');
        wrapper.style.height = `${wrapper.offsetHeight}px`;
        let wrapperPB = parseInt(window.getComputedStyle(wrapper).paddingBottom, 10);
        let offsetFromHeader = parseInt(window.getComputedStyle(header).top, 10) + header.offsetHeight + parseInt(window.getComputedStyle(header).marginBottom, 10);
        overlayForScroll.style.height = `${offsetFromHeader + sectionTitle.offsetHeight + wrapperPB}px`;
        overlayBottom.style.top = `${wrapper.offsetHeight}px`;
      }
      let fixedHeaderHeight = overlayForScroll.offsetHeight;
      let scrolledHeight;
      if(!window.matchMedia('(max-width: 600px)').matches){
        scrolledHeight = window.innerHeight;
      } else {
        scrolledHeight = container.querySelector('.js--content.main__wrapper--mobile-pt').offsetHeight;
      }
      const scrolledYHeader =  scrolledHeight - fixedHeaderHeight; 
      let isHeaderFixed;
      if(mainSectionTop){ //       
        isHeaderFixed = window.scrollY > (mainSectionTop.offsetHeight - fixedHeaderHeight);
      } else {
        isHeaderFixed = window.scrollY > scrolledYHeader;
      }
      if(mainSectionTop){
        overlayBottom.style.top = `${mainSectionTop.offsetHeight}px`
        overlayForScroll.style.top = `${mainSectionTop.offsetHeight - overlayForScroll.offsetHeight}px;`
      }
      if(isHeaderFixed){
        if(window.matchMedia('(max-width: 600px)').matches){
          overlayBottom.style.top = `${overlayForScroll.offsetHeight}px`;
          sectionTitle.classList.add('main__title--fixed');
        }
        overlayBottom.classList.add('overlay--bottom-fixed');
        overlayForScroll.classList.add('overlay--fixed');
        if(mainSectionTop){
          overlayBottom.style.removeProperty('top');
          overlayForScroll.style.top = `unset`;
        }
      } else {
        if(window.matchMedia('(max-width: 600px)').matches){
          overlayBottom.style.top = `${container.querySelector('.js--content.main__wrapper--mobile-pt').offsetHeight}px`;
          sectionTitle.classList.remove('main__title--fixed');
        }
        overlayBottom.classList.remove('overlay--bottom-fixed');
        overlayForScroll.classList.remove('overlay--fixed');
      }
      //   -        
      if(sectionTitle){
        let sectionTitleParent = sectionTitle.parentNode,
          stParentPB = parseInt(window.getComputedStyle(sectionTitleParent,null).getPropertyValue("padding-bottom"), 10),
          fixedSTOffset = overlayForScroll.offsetHeight + stParentPB,
          scrolledYST;
          // scrolledYST = window.innerHeight - fixedSTOffset;
          if(mainSectionTop){
            scrolledYST = mainSectionTop.offsetHeight - fixedSTOffset;
          } else {
            scrolledYST = window.innerHeight - fixedSTOffset;
          }
          if(window.scrollY > scrolledYST){
            sectionTitle.classList.add('main__title--scrolled');
            if(sectionTitleFixed){
              sectionTitleFixed.classList.add('main__title--fixed');
            }
          } else {
            sectionTitle.classList.remove('main__title--scrolled');
            if(sectionTitleFixed){
              sectionTitleFixed.classList.remove('main__title--fixed');
            }
          }
      }
    }
    fixingHeader(overlayForScroll, overlayBottom, sectionTitle, sectionTitleFixed);
    window.addEventListener('scroll', () => {
      fixingHeader(overlayForScroll, overlayBottom, sectionTitle, sectionTitleFixed);
    });
  }
  //     (.shows)   
  let showsItems = container.querySelectorAll('.js--shows__item');
  if(showsItems.length > 0){
    let showsLeadText = document.querySelectorAll('.js--shows__lead');
    showsItems = Array.from(showsItems).concat(Array.from(showsLeadText));
    function showShowsItem(showsItems, delay=0){
      let itemsToShow = [];
      showsItems.forEach(item => {
        if(checkElementOnScreen({
          element: item,
          threshold: {
            top: 3,
            bottom: 2
          }
        })){
          itemsToShow.push(item);
        }
      });
      itemsToShow.forEach((item, i) => {
        setTimeout(() => {
          if(item.classList.contains('shows__item')){
            item.classList.add('shows__item--visible');
          }
          if(item.classList.contains('shows__lead')){
            item.classList.add('shows__lead--visible');
          }
        }, i * delay);
      });
    }
    showShowsItem(showsItems, 1000);
    window.addEventListener('scroll', () => {
      showShowsItem(showsItems);
    });
  }
  // checkElementOnScreen
}

/*        */
export function addMenuListContentListeners(){
  let links = document.querySelectorAll('.js--menu-list__link--content'),
    sidenavPointers = document.querySelectorAll('.js--sidenav__pointer');
  if(links.length > 0 && sidenavPointers.length > 0){
    links.forEach((link) => {
      link.addEventListener('mouseenter', function(){
        sidenavPointers.forEach(function(pointer){
          pointer.classList.add('sidenav__pointer--hidden');
        });
      });
      link.addEventListener('mouseleave', function(){
        sidenavPointers.forEach(function(pointer){
          pointer.classList.remove('sidenav__pointer--hidden');
        });
      });
    });
  };
}

/*   .    */
export function addMenuClickListener(){
  let mlLinkNavs = document.querySelectorAll('.js--menu-list__link--nav');
  mlLinkNavs.forEach((link) => {
    link.addEventListener('click', function(){
      mlLinkNavs.forEach((item) => {
        item.classList.remove('menu-list__link--current');  
      });
      link.classList.add('menu-list__link--current');
    });
  })
}

/*       -     */
export function showSidenavPointers(){
  let sidenavPointers = document.querySelectorAll('.js--sidenav__pointer');
  function displayPointers(sidenavPointers){
    sidenavPointers.forEach(pointer => {
      let pointerHeight = pointer.offsetHeight,
        icon = pointer.querySelector('.js--sidenav__pointer-icon'),
        iconHeight = icon.offsetHeight;
      if(window.scrollY > pointerHeight/2 - iconHeight*4){
        pointer.style.removeProperty('opacity');
        pointer.classList.add('sidenav__pointer--hidden');
      } else {
        pointer.style.removeProperty('opacity');
        pointer.classList.remove('sidenav__pointer--hidden');
      }
    });
  };
  displayPointers(sidenavPointers);
  window.addEventListener('scroll', () => {
    displayPointers(sidenavPointers);
  });
}

/*        ,    */
export function navDotsHandler(data){
  let ds = data.next.container.dataset;
  let sectionIndex = ds.sectionIndex;
  let upperTitle = ds.upperTitle;
  let namespace = ds.barbaNamespace;
  let navDots = document.querySelector('.js--nav-dots');
  if(navDots){
    //     - ,   dataset barba- 
    function init(navDots, sectionsCount){
      let htmlString = ``;
      for(let i = 0; i < sectionsCount; i++){
        let str = `
          <div class="nav-dots__item js--nav-dots__item nav-dots__item--shrink">
            <div class="nav-dots__item-dot js--nav-dots__item-dot" data-index=${i}>
            </div>
          </div>
        `;
        htmlString += str;
      };
      navDots.innerHTML += htmlString;
      let navDotsItems = navDots.querySelectorAll('.js--nav-dots__item');
      
      navDotsItems.forEach(item => {
        item.classList.remove('nav-dots__item--shrink');
      });
    }
    function reInit(navDots, sectionsCount){
      let navDotsItems = navDots.querySelectorAll('.js--nav-dots__item');
      navDotsItems.forEach(item => {
        item.remove();
      });
      init(navDots, sectionsCount);
    }
    let sectionsCount;
    if(!ds.sectionsCount){
      if(window.sectionsCount){
        sectionsCount = window.sectionsCount;
      } else {
        sectionsCount = 0;
      }
    } else {
      sectionsCount = ds.sectionsCount;
      if(window.sectionsCount !== ds.sectionsCount){
        window.sectionsCount = ds.sectionsCount;
        reInit(navDots, window.sectionsCount);
      }
    }
    let navDotsItems = navDots.querySelectorAll('.js--nav-dots__item');
    //     -    (,   /service.html)
    if(!navDots.querySelector('.nav-dots__item--active') && navDotsItems.length > 0){
      navDotsItems[0].classList.add('nav-dots__item--active');
    };
    //         
    navDotsItems.forEach((item, index) => {
      if(index === parseInt(sectionIndex, 10)){
        item.classList.add('nav-dots__item--active');
      } else {
        item.classList.remove('nav-dots__item--active');
      }
    });
    if(namespace === 'home'){
      const tl = timeline();
      const left = !window.matchMedia('(max-width: 600px)').matches ? 0 : '-2px';
      tl.to(
        document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'),
        1, { left: left, ease: 'circ' }
      );
      navDotsItems.forEach(function(item){
        item.classList.add('nav-dots__item--shrink');
      });
      navDots.classList.add('nav-dots--hidden');
    } else {
      if(upperTitle === 'services'){
        navDots.classList.remove('nav-dots--hidden');
      } else {
        navDots.classList.add('nav-dots--hidden');
      }
    }
    if(namespace === 'section'){
      navDotsItems.forEach(function(item){
        item.classList.remove('nav-dots__item--shrink');
      });
      navDots.classList.remove('nav-dots--hidden');
    }
    if(namespace === 'page'){
      if(upperTitle === 'services'){
        const tl = timeline();
        const left = !window.matchMedia('(max-width: 600px)').matches ? 0 : '-2px';
        tl.to(
          document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'),
          1, { left: left, ease: 'circ' }
        );
        const method = !window.matchMedia("(max-width: 600px)").matches ? 'add' : 'remove';
        navDotsItems.forEach(function(item){
          item.classList[method]('nav-dots__item--shrink');
        });
      }
    }
  }
}

/*         */
export function setNavDotsMarker(){
  //     
  let navDotsMarker = document.querySelector('.js--nav-dots__marker');
  let activeDot = document.querySelector('.js--nav-dots__item.nav-dots__item--active');
  let offset;
  if(activeDot){
    offset = activeDot.offsetLeft;
  } else {
    offset = !window.matchMedia('(max-width: 600px)').matches ? 0 : '-2px';
  }
  let width;
  if(!window.matchMedia("(max-width: 600px)").matches){
    width = 12;
  } else {
    width = 10;
  }
  const tl = timeline();
  navDotsMarker.classList.remove('nav-dots__marker--to-prev');

  let transitionOpts = {width: width, ease: 'circ', onComplete: () => {
    navDotsMarker.classList.add('nav-dots__marker--inited');
  }};
  if(!window.matchMedia("(max-width: 600px)").matches){
    transitionOpts.left = offset;
  } else {
    transitionOpts.x = offset;
  }

  tl
    .to(navDotsMarker, 1, transitionOpts);
};

