export function initSectionTitleHover(container = document){
  const sectionTitle = container.querySelector('.js--main__title--section');
  if(sectionTitle && !window.matchMedia('(max-width: 600px)').matches){
    sectionTitle.addEventListener('mouseenter', () => {
      const tl = timeline();
      tl.addLabel('stMouseEnter')
        .to(sectionTitle, 1, {color: '#fff', ease: 'linear'}, 'stMouseEnter')
        .to('.js--sidenav__pointer:not(.sidenav__pointer--hidden)', 1, {opacity: 0, ease: 'linear'}, 'stMouseEnter')
        .to('.js--section-pattern__item-layer--fg', 1, {opacity: 1, ease: 'linear'}, 'stMouseEnter')
        .to('.js--section-pattern__item-layer--bg', 1, {opacity: 1, ease: 'linear'}, 'stMouseEnter');
      });
    sectionTitle.addEventListener('mouseleave', () => {
      const tl = timeline();
      tl.addLabel('stMouseLeave')
        .to(sectionTitle, 1, {color: '#0E0D13', ease: 'linear'}, 'stMouseLeave')
        .to('.js--sidenav__pointer:not(.sidenav__pointer--hidden)', 1, {opacity: 1, ease: 'linear'}, 'stMouseLeave')
        .to('.js--section-pattern__item-layer--fg', 1, {opacity: 0, ease: 'linear'}, 'stMouseLeave')
        .to('.js--section-pattern__item-layer--bg', 1, {opacity: 0, ease: 'linear'}, 'stMouseLeave');
      });
  }
}

/*    */
export function initCustomScroll(){
  const mainWrapperHr = document.querySelector('.js--main__wrapper--hr');
  if(mainWrapperHr){
    // -     
    function showShowsItem(showsItems, delay=0){
      let itemsToShow = [];
      showsItems.forEach(item => {
        if(checkElementOnScreen({
          element: item,
          threshold: {
            top: 3,
            bottom: 2,
          }
        })){
          itemsToShow.push(item);
        }
      });
      itemsToShow.forEach((item, i) => {
        setTimeout(() => {
          item.classList.add('shows__item--visible');
        }, i * delay);
      });
    };
    //      
    const showsItems = mainWrapperHr.querySelectorAll('.js--shows__item--horizontal');
    const scrollBar = OverlayScrollbars(mainWrapperHr, {
      className: 'os-theme-round-light',
      callbacks: {
        onInitialized: function(){
          const _self = this;
          function scrollHorizontally(e){
            e = window.event || e;
            let delta = Math.max(-1, Math.min(1, (e.wheelDelta || -e.detail)));
            let thisPositionX = _self.scroll().position.x;
            let koef = -40;
            _self.scroll({
              x: thisPositionX + delta*koef
            });
            e.preventDefault();
          }
          mainWrapperHr.addEventListener("mousewheel", scrollHorizontally, false);

          mainWrapperHr.classList.remove('main__wrapper--hidden');
          if(showsItems.length > 0){
            setTimeout(function(){
              showShowsItem(showsItems, 1000);
            }, 0);
          }
        },
        onScroll: function(){
          showShowsItem(showsItems);
        }
      }
    });
  }
}

/*       (, .     ..) */
export function checkInvertElements(container=document){
  const layout = document.querySelector('.js--layout');
  const tl = timeline();
  tl.addLabel('inverse');
  if(container.dataset.invertElements){
    layout.classList.add('layout--white');
    // tl.to(layout, 1, {
    //   backgroundColor: "#ffffff",
    //   ease: 'circ',
    // }, 'inverse');
  } else {
    layout.classList.remove('layout--white')
    // tl.to(layout, 1, {
    //   backgroundColor: "#0E0D13",
    //   ease: 'circ',
    // }, 'inverse');
  };
  let classMethod;
  if(container.dataset.invertElements){
    classMethod = 'add';
  } else {
    classMethod = 'remove';
  };
  let elementsArr = [
    {
      selector: '.js--logo__svg-layer--letter',
      className: 'logo__svg-layer--letter-inverse'
    },
    {
      selector: '.js--menu-list__link--nav',
      className: 'menu-list__link--inverse'
    },
    {
      selector: '.js--menu-list__link--mobile',
      className: 'menu-list__link--inverse'
    },
    {
      selector: '.js--link--feedback',
      className: 'link--inverse'
    },
    {
      selector: '.js--lang__item',
      className: 'lang__item--inverse'
    },
    {
      selector: '.js--input',
      className: 'input--inverse'
    },
    {
      selector: '.js--form__message',
      className: 'form__message--inverse'
    },
    {
      selector: '.js--popup .js--overlay__layer',
      className: 'overlay__layer--white'
    },
    {
      selector: '.js--menu-mobile__layer',
      className: 'overlay__layer--white'
    },
    {
      selector: '.js--popup js--overlay__layer',
      className: 'overlay--white'
    },
    {
      selector: '.js--burger',
      className: 'burger--inverse'
    },
    {
      selector: '.js--lang--mobile',
      className: 'lang--inverse'
    },
    {
      selector: '.js--header',
      className: 'header--mobile-inverse'
    },
    {
      selector: '.js--form__popup-trigger',
      className: 'form__popup-trigger--inverse'
    },
  ];
  elementsArr.forEach(function(element){
    const {selector,className} = element;
    const elements = layout.querySelectorAll(selector);
    elements.forEach(function(item){
      item.classList[classMethod](className);
    })
  });
}

export function addScrollByTitleClick(){
  console.log('addScrollByTitleClick');
  const overlayForScroll = document.querySelector('.js--overlay--for-scroll');
  const sectionTitle = document.querySelector('.js--main__title--section');
  const main = document.querySelector('.js--main');
  if(overlayForScroll && sectionTitle && main.dataset.upperTitle === 'services'){
    sectionTitle.addEventListener('click', function(){
      window.scrollTo({
        top: window.innerHeight - overlayForScroll.offsetHeight,
        behavior: "smooth"
      });
    })
  }
}