import "@babel/polyfill";
import gsap from 'gsap';
const { timeline } = gsap;

import { initPresentationText } from './presentation-text';
import { initCursorCustom } from './cursor-custom';
import addOverlayVideo from './youtube-video';
import { initFormValidation } from './form';
import Popup from './popup';
import { initCDSlider, initSliderMobile } from './sliders';
import { initYmaps } from './contacts';
import { 
  addSideLinksListeners,
  onSideNavMouseEnter,
  onSideNavMouseLeave,
  addMenuListContentListeners,
  showSidenavPointers,
  navDotsHandler,
  setNavDotsMarker,
  addScrollListener,
  initSectionTitleHover,
  initCustomScroll,
  addSectionLinkListener,
  checkInvertElements,
  addScrollByTitleClick,
} from './page-animations-functions';

/*   */
const offsetX = 104; //    ,  /
const headerColRight = document.querySelector('.js--header__col--right');

// Once hook    (  )
export function onOnce(transition, data){
  // console.log('onOnce');
  const layout = document.querySelector('.js--layout');
  const next = data.next;
  const content = next.container.querySelectorAll('.js--content');
  const overlayFade = next.container.querySelectorAll('.js--overlay-fade');
  const done = transition.async();
  setTimeout(() => {
    next.container.style.display = 'block';
  }, 0);
  const { namespace } = next;
  const tl = timeline();
  switch (namespace) {
    case 'home': //  
      tl.addLabel('tlHome')
        .fromTo( layout, 1, {opacity: 0}, { opacity: 1, ease: 'circ' }, 'tlHome')
        .to( content, 1.5, { opacity: 1, ease: 'circ' }, 'tlHome');
      break;
    case 'page': //  
      //  ,     
      checkInvertElements(next.container)
      //  ,       
      initSliderMobile({});
      tl.addLabel('tlPage')
        .fromTo( layout, 1, {opacity: 0}, { opacity: 1, ease: 'circ' }, 'tlPage')
        .to( content, 1.5, { opacity: 1, ease: 'circ' }, 'tlPage')
          //    -   
        .to('.js--menu-list__item--content', 0.8, { y: 0, ease: 'circ', onComplete: () => {
          try{
            done(); 
          } catch (e) {
            console.log(e);
          }
        } }, 'tlPage');
      break;
    case 'projects': //   
      tl.addLabel('tlProjects')
        .fromTo( layout, 1, {opacity: 0}, { opacity: 1, ease: 'circ' }, 'tlProjects')
        .to( content, 1.5, { opacity: 1, ease: 'circ', onComplete: done }, 'tlProjects');
      break;
    case 'section': //  
      let layerPrev = next.container.querySelector('.js--overlay__layer--prev');
      if(layerPrev){
        layerPrev.style.visibility = 'hidden';
      };
      //         ,     
      addScrollByTitleClick();
      //  ,     
      initCDSlider({});
      tl.addLabel('tlSection')
        .fromTo( layout, 1, {opacity: 0}, { opacity: 1, ease: 'circ' }, 'tlSection')
        .to( content, 1.5, { opacity: 1, ease: 'circ', }, 'tlSection')
        // .from( header, 1.5, { opacity: 0, ease: 'circ' }, 'tlSection')
        .to( overlayFade, 1.5, { opacity: 1, ease: 'circ', }, 'tlSection')
        .to( 
          next.container.querySelector('.js--overlay__layer--prev'), 2, { x: '-200%', ease: 'circ', 
          onComplete: function(){
            if(layerPrev){
              layerPrev.style.visibility = 'visible';
            }
            done();
          }
        }, 'tlSection');
      break;
  }
  //     
  navDotsHandler(data);
  //      
  showSidenavPointers();
  //        
  let ds = next.container.dataset;
  if(ds.upperTitle !== 'projects'){
    initSectionTitleHover();
  }
  //         ..
  addScrollListener();
  //  
  initCursorCustom();
  //  
  initCustomScroll();
  addOverlayVideo();
  //  
  initFormValidation();
  //      
  let popup = new Popup();
}

/*      */
export function onPageReload(data) {
  // console.log('onPageReload');
  const { next } = data;
  addSideLinksListeners();
  initPresentationText();
  initCursorCustom();
  addOverlayVideo();
  addMenuListContentListeners();
  setNavDotsMarker();
  showSidenavPointers();
  let ds = next.container.dataset;
  if(ds.upperTitle !== 'projects'){
    initSectionTitleHover(next.container);
  }
  addScrollListener(next.container);
  //         
  addSectionLinkListener(next.container);
  //  
  initCustomScroll();
  //  ,     
  setTimeout(() => {
    initCDSlider({});
    //  ,       
    initSliderMobile({});
  }, 0);
  //     
  initYmaps();
  //         ,     
  addScrollByTitleClick();

  const cursorCustom = document.querySelector('.js--cursor-custom');
  cursorCustom.classList.remove('cursor-custom--hover-next', 'cursor-custom--hover-prev', 'cursor-custom--hover-link', 'cursor-custom--hover-down');

  const upperTitle = data.next.container.dataset.upperTitle;
  let linksNav = document.querySelectorAll('.js--menu-list__link--nav');
  linksNav.forEach((link) => {
    if(link.dataset.upperTitle !== upperTitle){
      link.classList.remove('menu-list__link--current');
    } else {
      link.classList.add('menu-list__link--current');
    }
  });
  //        
  if(data.next.namespace === 'section' && upperTitle === 'projects'){
    const overlayBottom = document.querySelector('.js--overlay--bottom');
    const tl = timeline();
    tl.
      to(overlayBottom, 1, {
        opacity: 1, ease: 'circ'
      });
  }
  //           
  if(data.next.namespace === 'projects' && data.current.namespace === 'section'){
    const overlayBottom = document.querySelector('.js--overlay--bottom');
    overlayBottom.style.removeProperty('top');
    const tl = timeline();
    tl.
      to(overlayBottom, 2, {
        opacity: 1, ease: 'circ'
      });
  }
}

/* Leave hook     */
export function onHomeToPageLeave(transition, data){
  const { trigger, current, next } = data;
  const done = transition.async();
  const nextContainer = next.container;
  const tl = timeline();
  //    ,   
  if(window.popup){
    window.popup.form.close();
  }
  let offset = {trigger: offsetX};
  const layerNext = document.querySelector(".js--overlay__layer--next");
  const moColContact = document.querySelector(".js--overlay__col--contact");
  if (trigger.classList.contains('js--sidenav__link--next')) {
    trigger.closest('.js--sidenav').removeEventListener('mouseenter', onSideNavMouseEnter);
    trigger.closest('.js--sidenav').removeEventListener('mouseleave', onSideNavMouseLeave);
  } else {
    const sideNavs = document.querySelectorAll('.js--sidenav');
    offset.trigger = 0;
    sideNavs.forEach(function(item){
      item.removeEventListener('mouseenter', onSideNavMouseEnter);
      item.removeEventListener('mouseleave', onSideNavMouseLeave);
    });
  };
  //  ,     
  checkInvertElements(nextContainer)
  tl.addLabel('backwardNext')
    .to(trigger, 1, { x: offset.trigger, ease: 'circ' }, 'backwardNext')
    .to(headerColRight, 1, { x: 0, ease: 'circ' }, 'backwardNext')
    .to(moColContact, 1, { x: 0, ease: 'circ' }, 'backwardNext')
    .to(layerNext, 1, {x: '-100%', ease: 'circ', onComplete: done}, 'backwardNext');
  next.container.style.display = 'none';
}

/* Enter hook     */
export function onHomeToPageEnter(transition, data) {
  const tl = timeline();
  data.current.container.style.display = 'none';
  const content = data.next.container.querySelectorAll('.js--content');
  const layerPrev = data.next.container.querySelector('.js--overlay__layer--prev');
  // const header = document.querySelector('.js--header');
  const done = transition.async();
  setTimeout(() => {
    data.next.container.style.display = 'block';
  }, 0);
  tl.addLabel('content')
    .to(content, 1.5, { opacity: 1, ease: 'circ' }, 'content')
    .to(layerPrev, 1, { x: '-200%', ease: 'circ' }, 'content')
    // .from( header, 1.5, { opacity: 0, ease: 'circ' }, 'content')
    .to('.js--menu-list__item--content', 0.8, { y: 0, ease: 'circ', onComplete: done }, 'content');
};

/* Leave hook      */
export function onPageToPageLeave(transition, data) {
  const tl = gsap.timeline();
  const { trigger, current, next } = data;
  const done = transition.async();
  const nextContainer = next.container;
  const mainSectionTop = nextContainer.querySelector('.js--main__section--top');
  const currContainer = current.container;
  const overlayBottom = document.querySelector('.js--overlay--bottom');
  //    ,   
  if(window.popup){
    window.popup.form.close();
  }
  //        
  tl.addLabel('pgpgLeave');
  if(!mainSectionTop && currContainer.dataset.upperTitle === 'projects'){
    tl
      .to(overlayBottom, 0.5, {
        opacity: 0,
        onComplete: function(){
          overlayBottom.style.cssText = '';
        },
      }, 'pgpgLeave');
  }
  //  ,     
  checkInvertElements(nextContainer);
  tl
    .to(current.container, 1, {
      opacity: 0,
      ease: 'circ',
      onComplete: done,
    }, 'pgpgLeave');

  nextContainer.style.display = 'none';
}

/* Enter hook      */
export function onPageToPageEnter(transition, data) {
  // console.log('page to page enter');
  const tl = timeline();
  data.current.container.style.display = 'none';
  const nextContainer = data.next.container;
  const content = nextContainer.querySelectorAll('.js--content');
  // const header = document.querySelector('.js--header');
  const done = transition.async();
  setTimeout(() => {
    nextContainer.style.display = 'block';
  }, 0);
  tl
    .addLabel('content')
    // .from( header, 1.5, { opacity: 0, ease: 'circ' }, 'content')
    .to(
      content, 
      1.5, 
      {
        opacity: 1,
        ease: 'circ'
      }, 'content')
    //      -       
    .fromTo( nextContainer.querySelector('.js--overlay--video'), 1, 
      {opacity: 0}, 
      { opacity: 1, ease: 'linear' }, 'content')
    .fromTo( nextContainer.querySelector('.js--overlay--presentation-text'), 1, 
      {opacity: 0}, 
      { opacity: 1, ease: 'linear' }, 'content')
    .to('.js--menu-list__item--content', 
      0.8, 
      {
        y: 0,
        ease: 'circ',
        onComplete: done
      }, 'content');
}

/* Leave hook      */
export function onPageToSectionLeave(transition, data){
  const { trigger, current, next } = data;
  const done = transition.async();
  const tl = timeline();
  let offset = {trigger: offsetX};
  const overlayBottom = document.querySelector('.js--overlay--bottom');
  const layerNext = document.querySelector(".js--overlay__layer--next");
  const moColContact = document.querySelector(".js--overlay__col--contact");
  const nextContainer = next.container;
  //    ,   
  if(window.popup){
    window.popup.form.close();
  }
  //  trigger instanceof HTMLElement   ,  
  //      
  if (trigger instanceof HTMLElement && trigger.classList.contains('js--sidenav__link--next')) {
    trigger.closest('.js--sidenav').removeEventListener('mouseenter', onSideNavMouseEnter);
    trigger.closest('.js--sidenav').removeEventListener('mouseleave', onSideNavMouseLeave);
  } else {
    const sideNavs = document.querySelectorAll('.js--sidenav');
    sideNavs.forEach(function(item){
      item.removeEventListener('mouseenter', onSideNavMouseEnter);
      item.removeEventListener('mouseleave', onSideNavMouseLeave);
    });
    offset.trigger = 0;
  };
  const mainSectionTop = nextContainer.querySelector('.js--main__section--top');
  if(mainSectionTop){
    overlayBottom.style.top = `${mainSectionTop.offsetHeight}px`;
  }
  tl.addLabel('backwardNext_1')
    .to(trigger, 1, { x: offset.trigger, ease: 'circ' }, 'backwardNext_1')
    .to(headerColRight, 1, { x: 0, ease: 'circ' }, 'backwardNext_1')
    .to(moColContact, 1, { x: 0, ease: 'circ' }, 'backwardNext_1')
    .to(layerNext, 1, {x: '-100%', ease: 'circ', onComplete: function(){
      current.container.style.display = 'none';    
      done();
    }}, 'backwardNext_1');
}

/* Enter hook c (page)   (section) */
export function onPageToSectionEnter(transition, data) {
  const tl = timeline();
  const content = data.next.container.querySelectorAll('.js--content');
  // const header = document.querySelector('.js--header');
  const overlayFade = data.next.container.querySelectorAll('.js--overlay-fade');
  const layerPrev = data.next.container.querySelector(".js--overlay__layer--prev");
  const done = transition.async();
  setTimeout(() => {
    data.next.container.style.display = 'block';
  }, 0);
  tl.addLabel('content')
    .to(layerPrev, 
      1, {
        x: '-200%', 
        ease: 'circ',
    }, 'content')
    .to( overlayFade, 1.5, { opacity: 1, ease: 'circ' }, 'content')
    // .from( header, 1.5, { opacity: 0, ease: 'circ' }, 'content')
    .to(content, 
      1.5, {
        opacity: 1,
        ease: 'circ',
        onComplete: done,
    }, 'content');
  //  
  initCustomScroll();
}

/* Leave hook      */
export function onSectionToSectionLeave(transition, data){
  const { trigger, current, next } = data;
  const done = transition.async();
  const tl = timeline();
  //    ,   
  if(window.popup){
    window.popup.form.close();
  }
  const curContainer = data.current.container;
  const nextContainer = data.next.container;
  let offset = {trigger: offsetX};
  let layerMoved = document.querySelector(".js--overlay__layer--next");
  const moColContact = document.querySelector(".js--overlay__col--contact");
  const moColNavDots = document.querySelector(".js--overlay__col--nav-dots");
  const logo = document.querySelector(".js--logo");
  //      
  if (trigger.classList.contains('js--sidenav__link--next') || trigger.classList.contains('js--sidenav__link--prev')) {
    trigger.closest('.js--sidenav').removeEventListener('mouseenter', onSideNavMouseEnter);
    trigger.closest('.js--sidenav').removeEventListener('mouseleave', onSideNavMouseLeave);
    if(trigger.classList.contains('js--sidenav__link--prev')){
      offset.trigger = -1*offsetX;
      layerMoved = document.querySelector(".js--overlay__layer--prev");
    };
    if(scrollY > window.innerHeight){
      nextContainer.querySelector('.js--overlay--for-scroll').classList.add('overlay--fixed');
    }
  }
  //       
  if(trigger.classList.contains('js--main__link--to-page')){
    const triggerContent = trigger.querySelector('.js--main__link-text');
    trigger.classList.add('main__link--triggered');
    const layout = document.querySelector('.js--layout');
    const layerPrev = nextContainer.querySelector(".js--overlay__layer--prev");
    if(layerPrev){
      layerPrev.classList.remove('overlay__layer--front');
    }
    tl
      .addLabel('toProjects')
      .to(trigger.querySelector('.js--main__link-bg'), 1, {
        height: window.innerHeight, 
        width: window.innerWidth, 
        transformOrigin: '50% bottom', 
      }, 'toProjects')
      .to(triggerContent, 1, { y: 100, opacity: 0 }, 'toProjects')
      .to(curContainer.querySelector('.js--overlay__layer--bottom'), 1, {
        y: '0',
        ease: 'circ',
        onComplete: function(){
          current.container.style.display = 'none';
          layout.classList.add('layout--white');
          done();
        },
      }, 'toProjects+=1');
  } else {
    //      
    layerMoved.classList.add('js--overlay__layer--animated');
    tl.addLabel('backwardNext_1')
      .to(trigger, 1, { x: offset.trigger, ease: 'circ' }, 'backwardNext_1')
      .to(moColNavDots, 1, { x: 0, ease: 'circ' }, 'backwardNext_1')
      .to(headerColRight, 1, { x: 0, ease: 'circ' }, 'backwardNext_1')
      .to(logo, 1, { x: 0, ease: 'circ' }, 'backwardNext_1')
      .to(moColContact, 1, { x: 0, ease: 'circ' }, 'backwardNext_1')
      .to(layerMoved, 1, {x: '-100%', ease: 'circ', onComplete: function(){
        current.container.style.display = 'none';    
        done();
      }}, 'backwardNext_1');
  }

}

/* Enter hook c  (section)   (section) */
export function onSectionToSectionEnter(transition, data) {
  const { trigger, current, next } = data;
  const curContainer = data.current.container;
  const nextContainer = data.next.container;
  const content = nextContainer.querySelectorAll('.js--content');
  const upperTitle = nextContainer.dataset.upperTitle;
  let layerMoved = nextContainer.querySelector(".js--overlay__layer--prev");
  let overlayFade = nextContainer.querySelectorAll(".js--overlay-fade");
  const done = transition.async();
  //       ()
  if(upperTitle === 'services'){
    const animatedLayer = current.container.querySelector('.js--overlay__layer--animated');
    let offset = {
      layerMoved: '-200%'
    };
    // ..  ,     .    
    if(animatedLayer.classList.contains('js--overlay__layer--prev')){
      layerMoved.classList.remove('overlay__layer--front');
      if(trigger.classList.contains('js--sidenav__link--prev')){
        layerMoved.style.transform = `translate(${offset.layerMoved}, 0)`;
      }
      layerMoved = next.container.querySelector('.js--overlay__layer--next');
      layerMoved.classList.add('overlay__layer--front');
      offset.layerMoved = 0;
    }
    setTimeout(() => {
      nextContainer.style.display = 'block';
    }, 0);
    const tl = timeline();
    tl.addLabel('content')
      .to(layerMoved, 
        1, {
          x: offset.layerMoved, 
          ease: 'circ',
      }, 'content')
      .to( overlayFade, 1.5, { opacity: 1, ease: 'circ' }, 'content')
      .to(content, 
        1.5, {
          opacity: 1,
          ease: 'circ',
          onComplete: done,
      }, 'content');
  }
  //         
  if(upperTitle === 'projects'){
    setTimeout(() => {
      nextContainer.style.display = 'block';
    }, 0);
    window.scrollTo({
      top: 0,
      behavior: "instant"
    });
    const layout = document.querySelector('.js--layout');
    const tl = timeline();
    tl.addLabel('content');
    if(curContainer.dataset.upperTitle !== 'projects'){
      const layerPrev = nextContainer.querySelector('.js--overlay__layer--prev');
      layerPrev.classList.add('overlay__layer--white');
      tl.to( 
        layerPrev, 
        1, 
        { x: '-200%', ease: 'circ' }, 'content')
    }
    tl
      .fromTo( layout, 2, {opacity: 0}, { opacity: 1, ease: 'circ' }, 'content')
      .to(content, 
        1.5, 
        {
          opacity: 1,
          ease: 'circ',
          onComplete: done,
      }, 'content');
  }
}

/* Leave hook     */
export function onSectionToProjectsLeave(transition, data) {
  const tl = gsap.timeline();
  const { trigger, current, next } = data;
  const done = transition.async();
  //  trigger instanceof HTMLElement   ,  
  //      
  const overlayBottom = document.querySelector('.js--overlay--bottom');
  //    ,   
  if(window.popup){
    window.popup.form.close();
  }
  if(trigger instanceof HTMLElement && trigger.classList.contains('js--main__link--to-page')){
    const layout = document.querySelector('.js--layout');
    layout.classList.remove('layout--white');
    const triggerContent = trigger.querySelector('.js--main__link-text');
    trigger.classList.add('main__link--triggered');
    tl
      .addLabel('toProjects')
      .to(overlayBottom, 0.5, { opacity: 0, onComplete: done }, 'toProjects')
      .to(trigger.querySelector('.js--main__link-bg'), 1, {
        height: window.innerHeight, 
        width: window.innerWidth, 
        transformOrigin: '50% bottom', 
        onComplete: done,
      }, 'toProjects')
      .to(triggerContent, 1, { y: 100, opacity: 0 }, 'toProjects');
  } else {
    //  .   
    navDotsHandler(data);
    tl
      .addLabel('content')
      .to(overlayBottom, 0.5, { opacity: 0, onComplete: done }, 'content')
      .to(current.container, 1, { opacity: 0, onComplete: done }, 'content');
  }
}

/* Enter hook     */
export function onSectionToProjectsEnter(transition, data) {
  const tl = timeline();
  data.current.container.style.display = 'none';
  const content = data.next.container.querySelectorAll('.js--content');
  const done = transition.async();
  setTimeout(() => {
    data.next.container.style.display = 'block';
  }, 0);
  tl.addLabel('content')
    .from(content, 1, {
    opacity: 0,
    y: 50,
    onComplete: done,
  }, 'content');
  //  
  checkInvertElements(data.next.container);
}

/* Leave hook            */
export function onProjectsToSectionLeave(transition, data){
  const tl = gsap.timeline();
  const { trigger, current, next } = data;
  const done = transition.async();
  const curContainer = current.container;
  const layerPrev = curContainer.querySelector('.js--overlay__layer--prev');
  const nextContainer = next.container;
  const mainSectionTop = nextContainer.querySelector('.js--main__section--top');
  const overlayBottom = document.querySelector('.js--overlay--bottom');
  //    ,   
  if(window.popup){
    window.popup.form.close();
  }
  tl
    .addLabel('content');
  if(mainSectionTop){
    tl.to(overlayBottom, 0.5, {
      opacity: 0,
      ease: 'circ'
    }, 'content');
  }
  tl
    .to( 
      layerPrev, 
      1, 
      { 
        x: '-100%', 
        ease: 'circ',
      }, 'content')
    .to(current.container, 1, {
      opacity: 0,
      onComplete: done,
    }, 'content');
  next.container.style.display = 'none';
}
/* Enter hook            */
export function onProjectsToSectionEnter(transition, data){
  // console.log('projects to section enter');
  const tl = timeline();
  data.current.container.style.display = 'none';
  const nextContainer = data.next.container;
  const layerPrev = nextContainer.querySelector('.js--overlay__layer--prev');
  const content = nextContainer.querySelectorAll('.js--content');
  const overlayBottom = document.querySelector('.js--overlay--bottom');
  const done = transition.async();
  //  
  addScrollListener(nextContainer);
  setTimeout(() => {
    nextContainer.style.display = 'block';
  }, 0);
  tl
    .addLabel('content')
    .to( 
      layerPrev, 
      1, 
      { 
        x: '-200%', 
        ease: 'circ',
      }, 'content')
    .to(
      content, 
      1.5, 
      {
        opacity: 1,
        ease: 'circ',
        onComplete: done
      }, 'content');
}