import "@babel/polyfill";
import ymaps from 'ymaps';

export function initYmaps(){
  const mapContainer = document.querySelector('.js--contacts__map');
  if(!mapContainer){
    return;
  };
  ymaps
    .load()
    .then(maps => {
      const map = new maps.Map('map', {
        center: [55.775094, 37.506523],
        zoom: 16,
        controls: []
      });
      let circleLayout = maps.templateLayoutFactory
        .createClass('<div class="contacts__map-placemark"></div>');

      let circlePlacemark = new maps.Placemark(
          [55.775094, 37.506523], {
              iconCaption: '  ...',
              hintContent: '  ...'
          }, {
              iconLayout: circleLayout,
              //     "".
              iconShape: {
                  type: 'Circle',
                  //       
                  coordinates: [0, 0],
                  radius: 16
              }
          }
      );
      map.geoObjects.add(circlePlacemark);
      
    })
    .catch(error => console.log('Failed to load Yandex Maps', error));
}