import "@babel/polyfill";
export default () => {
  const inputs = document.querySelectorAll('.js--input');
  if (inputs.length === 0) {
    return;
  }
  inputs.forEach((input) => {
    input.addEventListener('mouseover', (e) => {
      e.target.closest('.input').classList.add('input--hovered');
    });

    input.addEventListener('mouseout', (e) => {
      e.target.closest('.input').classList.remove('input--hovered');
    });

    input.addEventListener('click', (e) => {
      const { target } = e;

      if (target.closest('.js--input-clear')) {
        if (input.classList.contains('input--textarea')) {
          target.closest('.js--input').querySelector('textarea').value = '';
        } else {
          target.closest('.js--input').querySelector('input').value = '';
        }
        target.closest('.input').classList.remove('input--success');
      }
    });
  });
};
