import "@babel/polyfill";
import gsap from 'gsap';

const { timeline } = gsap;

export default class Popup{
  constructor(options){
    let defaults = {
      selector: '.js--popup',
      dataPopup: 'form'
    };
    let settings = Object.assign({}, defaults, options)
    const { selector, dataPopup } = settings;
    let element = document.querySelector(selector);
    if(!element){
      return;
    }
    this.element = element;
    this.dataPopup = dataPopup;
    this.state = 'closed';
    if(!window.popup){
      window.popup = {}
    };
    this.initBindings();
    window.popup[dataPopup] = this;
  }
  initBindings(){
    const _self = this;
    let triggers = document.querySelectorAll(`.js--link--popup[data-popup="${_self.dataPopup}"]`);
    triggers.forEach(trigger => {
      trigger.addEventListener('click', function(){
        if(_self.state === 'closed'){
          _self.open();
        } else {
          _self.close();
        }
      });
    });
    this.triggers = triggers;
  }
  open(){
    const _self = this;
    this.state = 'open';
    let tl = timeline();
    this.triggers.forEach(trigger => {
      if(trigger.classList.contains('link--feedback')){
        trigger.classList.add('link--feedback-open');
      };
      if(trigger.classList.contains('form__popup-trigger')){
        trigger.classList.add('form__popup-trigger--open');
      };
      if(!window.matchMedia("(max-width: 600px)").matches){
        trigger.style.pointerEvents = 'none';
      }
    });
    this.element.classList.add('overlay--popup-open');
    const header = document.querySelector('.js--header');
    const layout = document.querySelector('.js--layout');
    const overlayBottom = document.querySelector('.js--overlay--bottom');
    if(overlayBottom.style && overlayBottom.style.top){
      this.overlayBottomTop = overlayBottom.style.top;
    };
    //      " /"
    let overlayBottomTop = window.getComputedStyle(overlayBottom,null).getPropertyValue("top");
    if(overlayBottomTop){
      this.overlayBottomTop = overlayBottomTop;
    };
    // .. ,  
    layout.classList.add('layout--cropped');
    // ..    
    header.classList.add('header--mobile-top');
    // ..    
    overlayBottom.style.top = '100vh';
    tl.addLabel('open')
    if(!window.matchMedia("(max-width: 600px)").matches){
      tl.to(document.querySelector('.js--overlay__col--nav-dots'), 0.5, 
        {opacity: 0, ease: 'circ'}, 'open')
    };
    tl.to(document.querySelector('.js--content.main__wrapper--slider'), 0.5, 
        {opacity: 0, ease: 'circ'}, 'open')
      .to(this.element, 0.5, {opacity: 1, ease: 'circ', onComplete: function(){
        if(!window.matchMedia("(max-width: 600px)").matches){
          _self.triggers.forEach(trigger => {
            trigger.style.pointerEvents = 'all';
          });
        }
      }}, 'open');
  }
  close(){
    const _self = this;
    this.state = 'closed';
    let tl = timeline();
    this.triggers.forEach(trigger => {
      if(trigger.classList.contains('link--feedback')){
        trigger.classList.remove('link--feedback-open');
      };
      if(trigger.classList.contains('form__popup-trigger')){
        trigger.classList.remove('form__popup-trigger--open');
      };
      trigger.style.pointerEvents = 'none';
    });
    this.element.classList.remove('overlay--popup-open');
    const header = document.querySelector('.js--header');
    const layout = document.querySelector('.js--layout');
    //      " /"
    const overlayBottom = document.querySelector('.js--overlay--bottom');
    if(this.overlayBottomTop){
      overlayBottom.style.top = `${this.overlayBottomTop}`;
    };
    //   
    layout.classList.remove('layout--cropped');
    // ..    
    header.classList.remove('header--mobile-top');
    tl.addLabel('close')
    if(!window.matchMedia("(max-width: 600px)").matches){
      tl.to(document.querySelector('.js--overlay__col--nav-dots'), 0.5, 
        {opacity: 1, ease: 'circ'}, 'close')
    };
    tl
      .to(document.querySelector('.js--content.main__wrapper--slider'), 0.5, 
        {opacity: 1, ease: 'circ'}, 'close')
      .to(this.element, 0.5, {opacity: 0, ease: 'circ', onComplete: function(){
        _self.triggers.forEach(trigger => {
          trigger.style.pointerEvents = 'all';
        });
      }}, 'close');
  }
}