import "@babel/polyfill";
import $ from 'jquery';
import slick from 'slick-carousel';
import gsap from 'gsap';

const { timeline } = gsap;

//      - project.html -> case-details.pug
export function initCDSlider(options){
  let { selector = '.js--case-details__slider' } = options;
  const element = document.querySelector(selector);
  if(!element){
    return;
  };
  let {
    infinite = false,
    draggable = false,
    slidesToShow = 1,
    slidesToScroll = 1,
    speed = 1000,
    easing = 'ease',
    centerMode = true,
    prevArrow = element.parentNode.querySelector('.js--case-details__slider-arrow--prev'),
    nextArrow = element.parentNode.querySelector('.js--case-details__slider-arrow--next'),
  } = options;
  let slickOptions = {
    slidesToShow,
    slidesToScroll,
    speed,
    centerMode,
    // draggable,
    easing,
  };
  if(prevArrow && nextArrow){
    slickOptions.prevArrow = prevArrow;
    slickOptions.nextArrow = nextArrow;
  }
  $(element)
    .on('init', () => {
      element.classList.remove('case-details__slider--disabled');
    })
    .on('beforeChange', (event, slick, currentSlide, nextSlide) => {
      const count = slick.slideCount;
      if(currentSlide + 2 > count){
        // ,   
        slick.$list[0].querySelector(`.slick-cloned[data-slick-index="${currentSlide + 1}"]`).classList.add('active');
      }
      if(currentSlide - 1 < 0){
        // ,   
        slick.$list[0].querySelector(`.slick-cloned[data-slick-index="${-1}"]`).classList.add('active');
      }
    })
    .on('afterChange', (event, slick, currentSlide, nextSlide) => {
      slick.$list[0].querySelectorAll(`.slick-cloned`).forEach(item => {
        item.classList.remove('active');
      })
    })
  $(element).not('.slick-initialized').slick(slickOptions);
}

//     -   
export function initSliderMobile(options){
  let { selector = '.js--slider-mobile' } = options;
  const parent = document.querySelector(selector);
  if(!parent || !window.matchMedia("(max-width: 600px)").matches){
    return;
  };
  const element = parent.querySelector('.js--slider-mobile__items');
  let {
    infinite = false,
    draggable = false,
    slidesToShow = 1,
    slidesToScroll = 1,
    speed = 1000,
    easing = 'ease',
    centerMode = true,
    prevArrow = parent.querySelector('.js--slider-mobile__arrow--prev'),
    nextArrow = parent.querySelector('.js--slider-mobile__arrow--next'),
  } = options;
  let slickOptions = {
    slidesToShow,
    infinite,
    slidesToScroll,
    speed,
    centerMode,
    easing,
  };
  if(prevArrow && nextArrow){
    slickOptions.prevArrow = prevArrow;
    slickOptions.nextArrow = nextArrow;
  };
  let oldSlide;
  $(element)
    .on('init', (event, slick) => {
      parent.classList.remove('slider-mobile--disabled');
    })
    .on('beforeChange', (event, slick, currentSlide, nextSlide) => {
      // console.log(currentSlide, nextSlide);
      const tl = timeline();
      const dotsItems = document.querySelectorAll('.js--nav-dots__item');
      oldSlide = currentSlide;
      let transitionOpts = {
        width: 36,
        ease: 'circ'
      }
      if(currentSlide > nextSlide){
        transitionOpts.x = dotsItems[nextSlide].offsetLeft - 2;
      }
      tl
        .to(document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'), 
          1, transitionOpts);
    })
    .on('afterChange', (event, slick, currentSlide) => {
      // console.log(currentSlide, nextSlide);
      const tl = timeline();
      const dotsItems = document.querySelectorAll('.js--nav-dots__item');
      const offset = dotsItems[currentSlide].offsetLeft;
        tl
          .to(document.querySelector('.js--nav-dots__marker.nav-dots__marker--inited'), 
            1, { width: 10, x: offset, ease: 'circ' });
    })
  $(element).not('.slick-initialized').slick(slickOptions);
}