import "@babel/polyfill";
export function checkElementOnScreen(options){
  //     
  let { element, threshold } = options;
  if(!element){
    return;
  };
  let thresholdDef = {
    top: 2, 
    bottom: 2, 
    left: 2, 
    right: 2, 
  };
  let optionsThreshold = Object.assign({}, thresholdDef, threshold);
  let {top: thrTop, bottom: thrBottom, left: thrLeft, right: thrRight} = optionsThreshold;
  let elementCR = element.getBoundingClientRect();
  let { top: elTop, bottom: elBottom, left: elLeft, right: elRight} = elementCR;
  let elementWidth = element.offsetWidth;
  let elementHeight = element.offsetHeight;
  let windowHeight = window.innerHeight;
  let windowWidth = window.innerWidth;
  let conditionRight = (windowWidth - elLeft)*thrLeft >= elementWidth;
  let conditionLeft = elRight*thrRight >= elementWidth;
  let conditionTop = (windowHeight - elTop)*thrTop >= elementHeight;
  let conditionBottom = elBottom*thrBottom >= elementHeight;
  let condTopLeft = conditionTop && conditionLeft;
  let condTopRight = conditionTop && conditionRight;
  let condBottomLeft = conditionBottom && conditionLeft;
  let condBottomRight = conditionBottom && conditionRight;
  return condTopLeft && condTopRight && condBottomLeft && condBottomRight;
  // }
}